	module.exports = {"data":{"markdownRemark":{"html":"<h2>Qubits</h2>\n<p>A Qubit is short for quantum bit, analogous to the classical computer’s binary digit (bit). It is the basic unit of information in a quantum computer, just like a binary digit (0 or 1) is the basic unit of information in classical computers widely in use today. </p>\n<p>The major difference between the classical bit and the quantum bit has to do with the amount of <strong>states</strong> they can exist in at a time which directly impacts on the amount of information they can hold. In a classical system, a bit would have to be in one state (0) or the other (1) at any one time. However, <strong>quantum mechanics</strong> allows the qubit to behave as if it were in both states simultaneously. In other words, each additional qubit utilized increases exponentially the amount of information the quantum computer can process or hold. This property is fundamental to quantum computing. </p>\n<p>To illustrate this further, if you had one qubit, you can store a “0” and “1” simultaneously (which corresponds to a total of 2 states or values). If you had a 2-qubit system, you can hold <strong>four</strong> values at once - 00, 01, 10, and 11. With a 3-qubit system, you can hold <strong>eight</strong> values at once - 000, 001, 010, 011, 100, 101, 110, and 111. In general, a quantum system consisting of n-qubits will be able to have 2^n states which is exponentially more powerful than any classical computer.</p>\n<h4>More Information</h4>\n<p><a href=\"https://en.wikipedia.org/wiki/Qubit\">Qbits on Wikipedia</a></p>\n<p><a href=\"https://en.wikipedia.org/wiki/Quantum_mechanics\">Quantum Mechanics on Wikipedia</a></p>","fields":{"slug":"/computer-science/quantum-computing/qubits/"},"frontmatter":{"title":"Qubits"}}},"pathContext":{"slug":"/computer-science/quantum-computing/qubits/"}}

